  width: 10%;
}

.faq__list .question-card label div img {
  margin: auto;
  transition: all .2s;
  display: block;
  transform: rotate(180deg);
}

.faq__list .question-card input ~ p {
  box-sizing: border-box;
  height: 0;
  opacity: 0;
  margin: 0;
  padding: 0 36px;
  transition: opacity .3s, height .3s;
  transform: translateX(-4000px);
}

.faq__list .question-card input:checked ~ label img {
  transform: rotate(0);
}

.faq__list .question-card input:checked ~ p {
  -webkit-backdrop-filter: blur(25px);
  backdrop-filter: blur(25px);
  height: auto;
  width: 100%;
  opacity: 1;
  background: #ffffff0d;
  border: none;
  border-radius: 20px;
  margin: 10px auto 0;
  padding: 20px;
  font-family: Inter;
  font-size: 20px;
  transform: none;
}

