.orbs {
  width: 100%;
  height: var(--wrapper-height);
  z-index: 0;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.orbs > svg {
  padding: 0;
  position: absolute;
}

.orbs > svg:nth-child(2n+1) {
  max-width: 25%;
  animation: 50s cubic-bezier(.45, .05, .55, .95) infinite fly;
}

@media (max-width: 800px) {
  .orbs > svg:nth-child(2n+1) {
    max-width: 50%;
    animation: 18s cubic-bezier(.45, .05, .55, .95) infinite flyMobile;
  }
}

.orbs > svg:nth-child(2n) {
  max-width: 25%;
  animation: 40s cubic-bezier(.45, .05, .55, .95) infinite flyReverse;
}

@media (max-width: 800px) {
  .orbs > svg:nth-child(2n) {
    max-width: 50%;
    animation: 16s cubic-bezier(.45, .05, .55, .95) infinite flyReverseMobile;
  }
}

.orbs > svg:nth-child(3n-1) {
  max-width: 15%;
  filter: blur(5px) grayscale();
}

.orbs > svg:nth-child(3n-2) {
  filter: blur(3px) grayscale(0);
  animation-delay: -4s;
}

.orbs > svg:nth-child(5n) {
  max-width: 60%;
  filter: blur() grayscale(0);
}

.orbs > svg:nth-child(5n-1) {
  filter: grayscale();
}

.orbs > svg:nth-child(3n) {
  animation-delay: -7s;
}

@keyframes fly {
  0%, 100% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(-1000px);
  }
}

@keyframes flyMobile {
  0%, 100% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(-400px);
  }
}

@keyframes flyReverse {
  0%, 100% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(1000px);
  }
}

@keyframes flyReverseMobile {
  0%, 100% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(400px);
  }
}

